.headerContainer {
  display: flex;
  width: 100%;
  height: 72px;
  padding: 14px 24px 13px 24px;

  align-items: center;

  border-radius: 0px 0px 8px 8px;
  border: 1.5px solid var(--black);

  background: var(--white);
}
.headerBurger {
  display: none;
}
.nav {
  display: flex;
  align-items: center;
}
.headerSwitch {
  display: flex;
  align-items: center;
}
.headerLogoLink {
  margin-right: 76px;
  color: var(--black);
}
.headerHomeNav {
  color: var(--black);
  font-size: 14px;

  font-weight: 500;
  line-height: 18px;
  letter-spacing: -0.56px;
  text-transform: uppercase;
  padding: 8px 16px;
  transition: all 0.3s;

  text-decoration: none;
}

.headerShopListNav {
  display: flex;
  align-items: center;
  margin-left: 24px;
  gap: 4px;
  color: var(--black);
  font-size: 14px;
  padding: 8px 16px;

  font-weight: 500;
  line-height: 1.28;
  letter-spacing: -0.56px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.3s;
}
.active {
  font-weight: 700;
  border-radius: 36px;
  background: var(--yellow);
  color: black;
}
.headerUserDrop {
  display: flex;
  align-items: center;
  margin-left: auto;
  gap: 24px;
}
.headerUserBtn {
  display: flex;
  border: inherit;
  padding: 4px 12px;
  border-radius: 36px;
  background: var(--violet);

  align-items: center;
}
.headerUserName {
  color: #fff;
  font-size: 22px;
  margin-left: 9px;
  margin-right: 4px;

  font-weight: 700;
  line-height: normal;
  letter-spacing: -0.44px;
}
.header {
  display: flex;
  width: 100%;
}
.headerBurgerBtn{
  display: none;
}

@media screen and (max-width: 1280px) {

  .headerContainer{
      padding: 14px 32px 13px 32px;

  }
  .headerLogoLink {
  margin-right: 40px;
 
}
   
  }

  @media screen and (max-width: 768px) {

  .headerContainer{
      padding: 18px 20px 18px 20px;

  }
  .headerLogoLink {
  margin-right: 0;
 
}
.headerHomeNav{
  display: none;
}
.headerShopListNav {
  display: none;
}
.headerUserBtn{
  display: none;
}
.headerUserDrop{
  gap:14px;
}
.headerBurgerBtn{
  cursor: pointer;
  border:none;
  padding: 0;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;

}
   
  }

  






